import { HTMLToJsonMLRule, MS_WORD } from '@ali/4ever-cangjie';
import parseFromWord from './parseFromWord';
import parseFromWeb from './parseFromWeb';

export default function htmlToJsoML(pasteFrom): HTMLToJsonMLRule {
  if (pasteFrom === MS_WORD) {
    return parseFromWord();
  }
  return parseFromWeb();
};
